.Button{background:transparent;display:flex;height:44px;align-items:center;justify-content:center;text-decoration:none;color:#fff;padding:20px;box-sizing:border-box;border:2px solid rgba(245,245,241,.2);border-radius:44px;font-size:14px;font-weight:600;transition:border .125s ease,background .125s ease}.Button:hover{border:2px solid #f5f5f1}.Button[data-primary=true]{border:2px solid #e50914}.Button[data-primary=true]:hover{background:#e50914}