.header{background-color:rgba(0,0,0,.8);height:6rem;top:0;left:0;color:#fff;width:100%;position:fixed;z-index:1;justify-content:space-around}.header,.header__logo{display:flex;align-items:center}.header__logo{width:30%;height:80%;justify-content:center}.header__logo__img{height:80%;width:12%;transition:1s ease-in-out}.header__logo__text{color:#fff;margin-left:5%;font-size:2rem;font-weight:900;letter-spacing:-.02em;line-height:1.2}.navbar{display:flex;align-items:center;width:35%;height:100%}.navbar__items{margin-left:3%}.navbar__link{cursor:pointer;font-size:20px;font-weight:400;transition:.1s ease-in-out}.navbar__link:hover{color:#1db954;transform:scale(.5)}